	module.exports = {"data":{"postsJson":{"title":"True and False Ministers","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","display":"block","bookName":"2 Corinthians","ref":"10:1-13:14","html":"<p>   <strong>10</strong><u>1</u>Now I Paul, myself, entreat you by the humility and gentleness of Christ; I who in your presence am lowly among you, but being absent am bold toward you. <u>2</u>Yes, I beg you that I may not, when present, show courage with the confidence with which I intend to be bold against some, who consider us to be walking according to the flesh. <u>3</u>For though we walk in the flesh, we don’t wage war according to the flesh; <u>4</u>for the weapons of our warfare are not of the flesh, but mighty before God to the throwing down of strongholds, <u>5</u>throwing down imaginations and every high thing that is exalted against the knowledge of God, and bringing every thought into captivity to the obedience of Christ; <u>6</u>and being in readiness to avenge all disobedience, when your obedience will be made full. <u>7</u>Do you look at things only as they appear in front of your face? If anyone trusts in himself that he is Christ’s, let him consider this again with himself, that, even as he is Christ’s, so also we are Christ’s. <u>8</u>For though I should boast somewhat abundantly concerning our authority, (which the Lord gave for building you up, and not for casting you down) I will not be disappointed, <u>9</u>that I may not seem as if I desire to terrify you by my letters. <u>10</u>For, “His letters”, they say, “are weighty and strong, but his bodily presence is weak, and his speech is despised.” <u>11</u>Let such a person consider this, that what we are in word by letters when we are absent, such are we also in deed when we are present. <u>12</u>For we are not bold to number or compare ourselves with some of those who commend themselves. But they themselves, measuring themselves by themselves, and comparing themselves with themselves, are without understanding. <u>13</u>But we will not boast beyond proper limits, but within the boundaries with which God appointed to us, which reach even to you. <u>14</u>For we don’t stretch ourselves too much, as though we didn’t reach to you. For we came even as far as to you with the Good News of Christ, <u>15</u>not boasting beyond proper limits in other men’s labors, but having hope that as your faith grows, we will be abundantly enlarged by you in our sphere of influence, <u>16</u>so as to preach the Good News even to the parts beyond you, not to boast in what someone else has already done. <u>17</u>But “he who boasts, let him boast in the Lord.”* <u>18</u>For it isn’t he who commends himself who is approved, but whom the Lord commends.<br />   <strong>11</strong><u>1</u>I wish that you would bear with me in a little foolishness, but indeed you do bear with me. <u>2</u>For I am jealous over you with a godly jealousy. For I married you to one husband, that I might present you as a pure virgin to Christ. <u>3</u>But I am afraid that somehow, as the serpent deceived Eve in his craftiness, so your minds might be corrupted from the simplicity that is in Christ. <u>4</u>For if he who comes preaches another Jesus, whom we did not preach, or if you receive a different spirit, which you did not receive, or a different “good news”, which you did not accept, you put up with that well enough. <u>5</u>For I reckon that I am not at all behind the very best apostles. <u>6</u>But though I am unskilled in speech, yet I am not unskilled in knowledge. No, in every way we have been revealed to you in all things. <u>7</u>Or did I commit a sin in humbling myself that you might be exalted, because I preached to you God’s Good News free of charge? <u>8</u>I robbed other assemblies, taking wages from them that I might serve you. <u>9</u>When I was present with you and was in need, I wasn’t a burden on anyone, for the brothers, when they came from Macedonia, supplied the measure of my need. In everything I kept myself from being burdensome to you, and I will continue to do so. <u>10</u>As the truth of Christ is in me, no one will stop me from this boasting in the regions of Achaia. <u>11</u>Why? Because I don’t love you? God knows. <u>12</u>But what I do, that I will do, that I may cut off occasion from them that desire an occasion, that in which they boast, they may be found even as we. <u>13</u>For such men are false apostles, deceitful workers, masquerading as Christ’s apostles. <u>14</u>And no wonder, for even Satan masquerades as an angel of light. <u>15</u>It is no great thing therefore if his servants also masquerade as servants of righteousness, whose end will be according to their works.<br />   <u>16</u>I say again, let no one think me foolish. But if so, yet receive me as foolish, that I also may boast a little. <u>17</u>That which I speak, I don’t speak according to the Lord, but as in foolishness, in this confidence of boasting. <u>18</u>Seeing that many boast after the flesh, I will also boast. <u>19</u>For you bear with the foolish gladly, being wise. <u>20</u>For you bear with a man, if he brings you into bondage, if he devours you, if he takes you captive, if he exalts himself, if he strikes you on the face. <u>21</u>I speak by way of disparagement, as though we had been weak. Yet in whatever way anyone is bold (I speak in foolishness), I am bold also. <u>22</u>Are they Hebrews? So am I. Are they Israelites? So am I. Are they the offspring* of Abraham? So am I. <u>23</u>Are they servants of Christ? (I speak as one beside himself) I am more so; in labors more abundantly, in prisons more abundantly, in stripes above measure, in deaths often. <u>24</u>Five times from the Jews I received forty stripes minus one. <u>25</u>Three times I was beaten with rods. Once I was stoned. Three times I suffered shipwreck. I have been a night and a day in the deep. <u>26</u>I have been in travels often, perils of rivers, perils of robbers, perils from my countrymen, perils from the Gentiles, perils in the city, perils in the wilderness, perils in the sea, perils among false brothers; <u>27</u>in labor and travail, in watchings often, in hunger and thirst, in fastings often, and in cold and nakedness.<br />   <u>28</u>Besides those things that are outside, there is that which presses on me daily, anxiety for all the assemblies. <u>29</u>Who is weak, and I am not weak? Who is caused to stumble, and I don’t burn with indignation? <u>30</u>If I must boast, I will boast of the things that concern my weakness. <u>31</u>The God and Father of the Lord Jesus Christ, he who is blessed forever more, knows that I don’t lie. <u>32</u>In Damascus the governor under King Aretas guarded the city of the Damascenes desiring to arrest me. <u>33</u>Through a window I was let down in a basket by the wall, and escaped his hands.<br />   <strong>12</strong><u>1</u>It is doubtless not profitable for me to boast. For I will come to visions and revelations of the Lord. <u>2</u>I know a man in Christ, fourteen years ago (whether in the body, I don’t know, or whether out of the body, I don’t know; God knows), such a one caught up into the third heaven. <u>3</u>I know such a man (whether in the body, or outside of the body, I don’t know; God knows), <u>4</u>how he was caught up into Paradise, and heard unspeakable words, which it is not lawful for a man to utter. <u>5</u>On behalf of such a one I will boast, but on my own behalf I will not boast, except in my weaknesses. <u>6</u>For if I would desire to boast, I will not be foolish; for I will speak the truth. But I refrain, so that no man may think more of me than that which he sees in me, or hears from me. <u>7</u>By reason of the exceeding greatness of the revelations, that I should not be exalted excessively, there was given to me a thorn in the flesh, a messenger of Satan to torment me, that I should not be exalted excessively. <u>8</u>Concerning this thing, I begged the Lord three times that it might depart from me. <u>9</u>He has said to me, “My grace is sufficient for you, for my power is made perfect in weakness.” Most gladly therefore I will rather glory in my weaknesses, that the power of Christ may rest on me.<br />   <u>10</u>Therefore I take pleasure in weaknesses, in injuries, in necessities, in persecutions, in distresses, for Christ’s sake. For when I am weak, then am I strong. <u>11</u>I have become foolish in boasting. You compelled me, for I ought to have been commended by you, for in nothing was I inferior to the very best apostles, though I am nothing. <u>12</u>Truly the signs of an apostle were worked among you in all patience, in signs and wonders and mighty works. <u>13</u>For what is there in which you were made inferior to the rest of the assemblies, unless it is that I myself was not a burden to you? Forgive me this wrong.<br />   <u>14</u>Behold, this is the third time I am ready to come to you, and I will not be a burden to you; for I seek not your possessions, but you. For the children ought not to save up for the parents, but the parents for the children. <u>15</u>I will most gladly spend and be spent for your souls. If I love you more abundantly, am I loved the less? <u>16</u>But be it so, I did not myself burden you. But, being crafty, I caught you with deception. <u>17</u>Did I take advantage of you by anyone of them whom I have sent to you? <u>18</u>I exhorted Titus, and I sent the brother with him. Did Titus take any advantage of you? Didn’t we walk in the same spirit? Didn’t we walk in the same steps? <u>19</u>Again, do you think that we are excusing ourselves to you? In the sight of God we speak in Christ. But all things, beloved, are for your edifying. <u>20</u>For I am afraid that by any means, when I come, I might find you not the way I want to, and that I might be found by you as you don’t desire; that by any means there would be strife, jealousy, outbursts of anger, factions, slander, whisperings, proud thoughts, riots; <u>21</u>that again when I come my God would humble me before you, and I would mourn for many of those who have sinned before now, and not repented of the uncleanness and sexual immorality and lustfulness which they committed.<br />   <strong>13</strong><u>1</u>This is the third time I am coming to you. “At the mouth of two or three witnesses shall every word be established.”* <u>2</u>I have said beforehand, and I do say beforehand, as when I was present the second time, so now, being absent, I write to those who have sinned before now, and to all the rest, that, if I come again, I will not spare; <u>3</u>seeing that you seek a proof of Christ who speaks in me; who toward you is not weak, but is powerful in you. <u>4</u>For he was crucified through weakness, yet he lives through the power of God. For we also are weak in him, but we will live with him through the power of God toward you. <u>5</u>Examine your own selves, whether you are in the faith. Test your own selves. Or don’t you know as to your own selves, that Jesus Christ is in you?—unless indeed you are disqualified. <u>6</u>But I hope that you will know that we aren’t disqualified.<br />   <u>7</u>Now I pray to God that you do no evil; not that we may appear approved, but that you may do that which is honorable, though we are as reprobate. <u>8</u>For we can do nothing against the truth, but for the truth. <u>9</u>For we rejoice when we are weak and you are strong. And this we also pray for, even your perfecting. <u>10</u>For this cause I write these things while absent, that I may not deal sharply when present, according to the authority which the Lord gave me for building up, and not for tearing down.<br />   <u>11</u>Finally, brothers, rejoice. Be perfected, be comforted, be of the same mind, live in peace, and the God of love and peace will be with you. <u>12</u>Greet one another with a holy kiss. <u>13</u>All the saints greet you. <u>14</u>The grace of the Lord Jesus Christ, the love of God, and the fellowship of the Holy Spirit, be with you all. Amen.</p><h5>*2 Corinthians 10:17 Jeremiah 9:24<br />*2 Corinthians 11:22 or, seed<br />*2 Corinthians 13:1 Deuteronomy 19:15</h5>","path":"/381","Question1":"List all the many questions raised in this passage. Who makes them? To whom are they made?","Question2":"Is there a question that impacts you more than others? Why?","Question3":"What questions would do you have about issues raised in this passage?","Question4":"What can you do to get some answers to these questions you have raised?","prevPath":"/380","nextPath":"/382"}},"pathContext":{}}

